<template>
    <ul class="todo-main">
     <!-- <li v-for="todo in todos">
         {{todos.title}}
     </li> -->
     <todo-item v-for="(todo, index) in todos" :key="index" :todo="todo" :index="index" :deleteTodo="deleteTodo"></todo-item>
    </ul>
    
</template>

<script>
import TodoItem from "./TodoItem.vue"
export default {
    props:{
        todos:Array,
        deleteTodo: Function
    },
    components:{
        TodoItem
    }
    
}
</script>


<style>
/*main*/
.todo-main {
  margin-left: 0px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0px;
}

.todo-empty {
  height: 40px;
  line-height: 40px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding-left: 5px;
  margin-top: 10px;
}



