<template>
  <div class="todo-container">
    <div class="todo-wrap">
     <TodoHeader ref="header"/>  <!-- 给todoHeader标签绑定addTodo事件监听   -->
    <TodoList :todos="todos" :deleteTodo="deleteTodo"/>
    <TodoFooter :todos="todos" :deleteCompleteTodos="deleteCompleteTodos" :selectAllTodos="selectAllTodos"/>

    
    </div>
  </div>

</template>

<script>

    import TodoHeader from './components/TodoHeader'
    import TodoFooter from './components/TodoFooter'
    import TodoList from './components/TodoList'

export default {
    data(){
        return{
          /* 从localstorage读取todos，一旦todos发生改变就能发现，深度监视
             getItem:Key对应value，以文本 字符串形式储存
             JSON格式字符串 才 转换成 数组;存了就解析todos_key，没存就解析后面的[]就能得到空数组 */
            todos: JSON.parse(window.localStorage.getItem('todos_key') || '[]')

        }
    },

    mounted(){//执行异步代码
    //this.$on('addTodo',this.addTodo)  给APP绑定的监听，应该是给TodoHeader下的addTodo绑定
      this.$refs.header.$on('addTodo',this.addTodo)
    },
    watch: {//深度监视
      todos:{
        deep:true, //是否为“深度”监视
        handler:function(value){
          //将todos最新值的json数据，保存到localstorage里面去
          window.localStorage.setItem('todos_key',JSON.stringify(value))
        }
      }
    },
    
    components:{
        TodoHeader,
        TodoFooter,
        TodoList
    },
    methods : {
        addTodo (todo) {
            this.todos.unshift(todo)
        },
        deleteTodo(index){
          this.todos.splice (index, 1 )
        },
        //删除所有选中的todo
        deleteCompleteTodos(){
          if(window.confirm("确认删除所有选中吗？")){
              { this.todos =  this.todos.filter(todo => !todo.complete)}
          }
        },
        //  全选/全部选
        selectAllTodos(check){
          this.todos.forEach (todo =>todo.complete = check)
        }
    }
}
</script>


<style>
/*app*/
.todo-container {
  width: 600px;
  margin: 0 auto;
}
.todo-container .todo-wrap {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

</style>


