<template>
        <li @mouseenter="handleEnter(true)"  @mouseleave="handleEnter(false)" :style="{background: bgColor}">
          <label>
            <input type="checkbox" v-model="todo.complete"/>
            <span>{{todo.title}}</span>
          </label>
          <button class="btn btn-danger" style="display:none;" v-show="isShow" @click="deleteItem">删除</button>         
        </li></template>

<script>
export default {
    props:{
        todo:Object,
        deleteTodo: Function,
        index:Number
    },
    data () {
      return {
        isShow: false  ,
        bgColor:'white' //默认的背景颜色
      }
    },
    methods:{
          handleEnter(isEnter){
             if(isEnter){
                 this.bgColor = "gray"
                 this.isShow = true
                 }
            else {
                 this.bgColor = "white"  //鼠标移出 按钮响应时
                 this.isShow = false

                 }
          },
          deleteItem() {
            const {todo,index,deleteTodo} = this
            if(window.confirm("确认删除吗？")){
              deleteTodo(index)
            }
          }
    }
    
}
</script>


<style>
/*item*/
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  margin-top: 3px;
  display: none;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}
</style>


